.header {
  margin-left: auto;
  margin-right: auto;
  background-color: black;
  width: 100%;
  height: 200px;
  background-size: 450px;
  display: flex;
  justify-content: center;
  align-items: center;

  &__img {
    object-fit: cover;
    width: 500px;

    @media all and (max-width: 768px) {
      width: 280px;
    }
  }
  @media all and (max-width: 768px) {
    height: 120px;
  }
}
